import { createRouter, createWebHashHistory } from 'vue-router'
import cache from '@/common/cache'

const LoginController = () => import('@/main-module/login/controller/LoginController')
const WebViewController = () => import('@/main-module/login/controller/WebViewController')
const TabBarController = () => import('@/main-module/TabBarController')
const WorkbenchController = () => import('@/main-module/management/workbench/controller/WorkbenchController')
const UserController = () => import('@/main-module/management/user/controller/UserController')
const CustomerHotlineController = () => import('@/main-module/management/user/controller/CustomerHotlineController')
const PersonalInfoController = () => import('@/main-module/management/user/controller/PersonalInfoController')
const SettingController = () => import('@/main-module/management/user/controller/SettingController')
const TestController = () => import('@/main-module/TestController')
const AllMatterController = () => import('@/main-module/management/workbench/controller/AllMatterController')
const AllMatterEditorController = () => import('@/main-module/management/workbench/controller/AllMatterEditorController')
const AllModuleEditorController = () => import('@/main-module/management/workbench/controller/AllModuleEditorController')

const HomeController = () => import('@/main-module/user-side/home/controller/HomeController')
const ClassifyController = () => import('@/main-module/user-side/classify/controller/ClassifyController')
const EngineeringController = () => import('@/main-module/user-side/engineering/controller/EngineeringController')
const ShoppingCartController = () => import('@/main-module/user-side/shopping-cart/controller/ShoppingCartController')
const SideUserController = () => import('@/main-module/user-side/user/controller/UserController')
const routes = [
  {
    path: '/',
    redirect: 'tabBar'
  },
  {
    path: '/login',
    name: 'login',
    component: LoginController,
    meta: {
      requireAuth: false,
      keepAlive: false,
      title: '登录'
    }
  },
  {
    path: '/allMatterEditor',
    name: 'allMatterEditor',
    component: AllMatterEditorController,
    meta: {
      requireAuth: true,
      keepAlive: false,
      title: '待办事项'
    }
  },
  {
    path: '/allModule',
    name: 'allModule',
    component: AllModuleEditorController,
    meta: {
      requireAuth: true,
      keepAlive: false,
      title: '应用中心'
    }
  },
  {
    path: '/allMatter',
    name: 'allMatter',
    component: AllMatterController,
    meta: {
      requireAuth: true,
      keepAlive: false,
      title: '全部事项'
    }
  },
  {
    path: '/test',
    name: 'test',
    component: TestController,
    meta: {
      requireAuth: false,
      keepAlive: false,
      title: '测试'
    }
  },
  {
    path: '/webView',
    name: 'webView',
    component: WebViewController,
    meta: {
      requireAuth: false,
      keepAlive: false,
      title: 'webView'
    }
  },
  {
    path: '/personalInfo',
    name: 'personalInfo',
    component: PersonalInfoController,
    meta: {
      requireAuth: true,
      keepAlive: false,
      title: '个人信息'
    }
  },
  {
    path: '/setting',
    name: 'setting',
    component: SettingController,
    meta: {
      requireAuth: true,
      keepAlive: false,
      title: '设置'
    }
  },
  {
    path: '/customerHotline',
    name: 'customerHotline',
    component: CustomerHotlineController,
    meta: {
      requireAuth: true,
      keepAlive: false,
      title: '客服热线'
    }
  },
  {
    path: '/tabBar',
    name: 'tabBar',
    component: TabBarController,
    redirect: () => {
      if (cache.value('appType') === 'user') {
        return 'tabBar/home'
      }
      return 'tabBar/workbench'
    },
    meta: {
      requireAuth: true,
      keepAlive: true,
      title: '主页'
    },
    children: [
      {
        path: 'workbench',
        name: 'workbench',
        component: WorkbenchController,
        meta: {
          icon: require('@/assets/management_img/tabbar-工作台-nor@2x.png'),
          selectedIcon: require('@/assets/management_img/tabbar-工作台-pre@2x.png'),
          requireAuth: true,
          keepAlive: true,
          title: '工作台'
        }
      },
      {
        path: 'user',
        name: 'user',
        component: UserController,
        meta: {
          icon: require('@/assets/management_img/tabbar-我的-nor@2x.png'),
          selectedIcon: require('@/assets/management_img/tabbar-我的-pre@2x.png'),
          requireAuth: true,
          keepAlive: true,
          title: '我的'
        }
      },
      {
        path: 'home',
        name: 'home',
        component: HomeController,
        meta: {
          icon: require('@/assets/user_side_img/tabbar_首页_nor@2x.png'),
          selectedIcon: require('@/assets/user_side_img/tabbar_首页_pre@2x.png'),
          requireAuth: true,
          keepAlive: true,
          title: '首页',
          isUserSide: true
        }
      },
      {
        path: 'classify',
        name: 'classify',
        component: ClassifyController,
        meta: {
          icon: require('@/assets/user_side_img/tabbar_分类_nor@2x.png'),
          selectedIcon: require('@/assets/user_side_img/tabbar_分类_pre@2x.png'),
          requireAuth: true,
          keepAlive: true,
          title: '分类',
          isUserSide: true
        }
      },
      {
        path: 'engineering',
        name: 'engineering',
        component: EngineeringController,
        meta: {
          icon: require('@/assets/user_side_img/tabbar_工程机_nor@2x.png'),
          selectedIcon: require('@/assets/user_side_img/tabbar_工程机_pre@2x.png'),
          requireAuth: true,
          keepAlive: true,
          title: '工程机',
          isUserSide: true
        }
      },
      {
        path: 'shoppingCart',
        name: 'shoppingCart',
        component: ShoppingCartController,
        meta: {
          icon: require('@/assets/user_side_img/tabbar_购物车_nor@2x.png'),
          selectedIcon: require('@/assets/user_side_img/tabbar_购物车_pre@2x.png'),
          requireAuth: true,
          keepAlive: true,
          title: '购物车',
          isUserSide: true
        }
      },
      {
        path: 'sideUser',
        name: 'sideUser',
        component: SideUserController,
        meta: {
          icon: require('@/assets/user_side_img/tabbar_我的_nor@2x.png'),
          selectedIcon: require('@/assets/user_side_img/tabbar_我的_pre@2x.png'),
          requireAuth: true,
          keepAlive: true,
          title: '我的',
          isUserSide: true
        }
      }
    ]
  }
]

const router = createRouter({
  // history: createWebHistory(process.env.BASE_URL),
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})
router.beforeEach((to, from, next) => {
  if (to.name === 'login' || !to.meta.requireAuth) {
    next()
    return
  }
  if (to.meta.requireAuth && cache.value('token')) {
    next()
  } else {
    next({ name: 'login' })
  }
})

export default router
