import {
  Button, Popup, Checkbox, NoticeBar, SwipeItem, Tab, Tabs, ActionSheet,
  DropdownMenu, DropdownItem, Swipe, CountDown, Progress
} from 'vant'

const models = [
  Button,
  Popup,
  Checkbox,
  NoticeBar,
  SwipeItem,
  Tab,
  Tabs,
  ActionSheet,
  DropdownMenu,
  DropdownItem,
  Swipe,
  SwipeItem,
  CountDown,
  Progress
]

const register = app => {
  models.forEach((value) => {
    app.use(value)
  })
}

export default register
