class Cache {
  value (key) {
    return localStorage.getItem(key)
  }

  setValue (key, value) {
    localStorage.setItem(key, value)
  }

  remove (key) {
    localStorage.removeItem(key)
  }

  saveToken (value) {
    this.setValue('token', value)
  }

  token () {
    this.value('token')
  }

  removeToken () {
    this.remove('token')
  }
}

export default new Cache()
